html, body {
  height: 100%;
  margin: 0;
}

body {
  font-family: 'Oxygen', sans-serif;
  font-size: 16px;
  color: #454545;
}

figure {
  margin: 0;
}

figcaption {
  color: #9e9e9e;
  font-style: italic;
}

.site-navigation {
  background-color: white;
  margin: 48px 0 0;
  padding: 20px 20vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
  position: relative;
}

.site-navigation .nav-title a {
  color: #454545;
  font-size: 24px;
  text-decoration: none;
}

.nav-content .nav {
  display: flex;
  font-size: 16px;
  margin: 0;
}

.nav li {
  list-style-type: none;
  margin-right: 60px;
}

.nav li:last-child {
  margin-right: 0;
}

.nav li a {
  color: #9e9e9e;
  text-decoration: none;
  transition: 250ms color;
}

.nav li a:hover {
  color: #454545;
}

.site-main {
  margin: 0 20vw;
}

.featured-posts {
  margin-top: 40px;
}

.featured-post {
  background-size: cover;
  border-radius: 8px;
  position: relative;
  margin: auto;
  height: 420px;
  width: 100%;
  transition: transform 250ms, box-shadow 250ms;
}

.featured-post::after {
  background: linear-gradient(14deg, rgba(14,14,14,1) 0%, rgba(227, 181, 54, 0.4) 100%);
  border-radius: 8px;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.featured-post:hover {
  box-shadow: 8px 8px #e3b536;
  transform: translateY(-8px);
}

.featured-post:hover .post-card-title {
  color: #e3b536;
}

.featured-post-tag {
  background-color: #e3b536;
  border-radius: 0 4px 4px 0;
  font-size: 14px;
  font-weight: 700;
  color: white;
  display: inline-block;
  padding: 8px 8px 8px 16px;
  position:relative;
  left: 0;
  top: 24px;
  text-transform: uppercase;
  z-index: 2;
}

.featured-post-content {
  position: absolute;
  margin: 0;
  bottom: 36px;
  width: 100%;
  z-index: 2;
  color: white;
  text-align: center;
}

.featured-post-content .post-card-title {
  font-family: 'Lilita One', cursive;
  font-size: 48px;
  font-weight: 400;
  margin: 16px auto;
  width: 72%;
}

.featured-post-content .post-tagline {
  color: #cccccc;
  margin: auto;
  width: 72%;
}

.featured-post-content .post-card-excerpt {
  margin: 24px auto;
  text-align: left;
  width: 72%;
}

.latest-posts {
  margin: 40px 5vw;
}

.post-preview {
  padding: 40px 0;
  border-bottom: 1px solid #dadada;
  position: relative;
}

.post-preview:first-child {
  border-top: 1px solid #dadada;
}

.post-continue {
  color: #e3b536;
  display: none;
}

.post-preview:hover .post-title {
  color: #e3b536;
}

.post-preview:hover .post-continue {
  display: inline;
}

.post-preview .post-tagline {
  color: #9e9e9e;
  margin: 0;
}

.post-preview .post-content {
  color: #454545;
  text-decoration: none;
}

.post-preview .post-title {
  margin: 8px 0;
}

.post-preview .post-excerpt {
  margin: 0;
}

.post-tagline {
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
}

.post-tagline .dot-suffix::after {
  content: "•";
  margin: 0 8px;
}

.post-tagline .primary-tag {
  font-weight: 700;
  color: #e3b536;
  text-decoration: none;
}

.post-tagline .primary-tag:hover {
  text-decoration: underline;
}

.pagination {
  font-weight: 700;
  text-align: center;
}

.newer-posts, .older-posts {
  color: #9e9e9e;
  text-decoration: none;
  transition: 250ms color;
}

.newer-posts:hover, .older-posts:hover {
  color: #454545;
}

.newer-posts {
  margin-right: 16px;
}

.older-posts {
  margin-left: 16px;
}

.site-footer {
  font-size: 14px;
  margin: 80px 0;
  text-transform: uppercase;
}

.copyright {
  font-size: 12px;
  text-align: center;
  padding: 0 20px;
}

.copyright a {
  color: #e3b536;
  text-decoration: none;
}

.copyright a:hover {
  text-decoration: underline;
}

.post-full {
  position: relative;
}

.post-full-header {
  margin: 40px 5vw;
  text-align: center;
}

.post-full-header .post-tagline {
  color: #9e9e9e;
  margin: 0;
}

.post-full-header .post-full-title {
  font-size: 48px;
  margin-top: 8px;
  margin-bottom: 24px;
}

.post-full-image, .post-full-image img {
  width: 100%;
}

.post-full-content {
  margin: 40px 5vw;
}

.post-content h2, .post-content h3, .post-content h4 {
  line-height: 2;
  margin-top: 32px;
}

.post-content p, .post-content li {
  line-height: 1.8;
}

.post-content a {
  color: #e3b536;
  font-weight: 700;
  text-decoration: none;
}

.post-content a:hover {
  text-decoration: underline;
}

.post-content pre {
  margin: 36px 0;
}

.post-content blockquote {
  margin: 0;
  padding: 8px 16px;
  border-left: 4px solid #e3b536;
}

.kg-bookmark-card {
  width: 100%;
}

.kg-card {
  margin: 36px 0;
  text-align: center;
}

.post-full-content .kg-bookmark-container {
  display: flex;
  min-height: 148px;
  color: #454545;
  text-decoration: none;
  border-radius: 3px;
  border: 1px solid #9e9e9e;
}

.kg-bookmark-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 20px;
}

.kg-bookmark-title {
  font-size: 18px;
  font-weight: 700;
  transition: color 250ms;
}

.kg-bookmark-container:hover {
  text-decoration: none;
}

.post-full-content .kg-bookmark-container:hover .kg-bookmark-title {
  color: #e3b536;
}

.kg-bookmark-description {
  display: -webkit-box;
  font-size: 14px;
  overflow-y: hidden;
  margin-top: 12px;
  max-height: 48px;

  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.kg-bookmark-thumbnail {
  position: relative;
  min-width: 33%;
  max-height: 100%;
}

.kg-bookmark-thumbnail img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 0 3px 3px 0;

  object-fit: cover;
}

.kg-bookmark-metadata {
  display: flex;
  font-size: 14px;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 14px;
}

.post-full-content .kg-bookmark-icon {
  margin-right: 5px;
  width: 14px;
  height: 14px;
}

.kg-bookmark-author {
  line-height: 1.5em;
}

.kg-bookmark-author:after {
  content: "•";
  margin: 0 6px;
}

.kg-bookmark-publisher {
  overflow: hidden;
  max-width: 240px;
  line-height: 1.5em;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.kg-image {
  max-width: 100%;
}

.kg-embed-card iframe {
  width: 57.6vw;
  height: 32.4vw;
}

.kg-gallery-container {
  display: flex;
  flex-direction: column;
  width: 70vw;
  position: relative;
  left: -5vw;
}

.kg-gallery-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.kg-gallery-image img {
  display: block;
  margin: 0;
  width: 100%;
  height: 100%;
}

.kg-gallery-row:not(:first-of-type) {
  margin: 0.75em 0 0 0;
}

.kg-gallery-image:not(:first-of-type) {
  margin: 0 0 0 0.75em;
}

.kg-gallery-card + .kg-image-card.kg-width-wide,
.kg-gallery-card + .kg-gallery-card,
.kg-image-card.kg-width-wide + .kg-gallery-card,
.kg-image-card.kg-width-wide + .kg-image-card.kg-width-wide {
  margin: -2.25em 0 3em;
}

.post-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 80px 0 40px 0;
}

.post-navigation a {
  text-decoration: none;
}

.post-navigation a:hover .previous-post-title, .post-navigation a:hover .next-post-title {
  color: #e3b536;
}

.post-navigation a:hover .post-continue {
  color: #e3b536;
  display: inline-block;
}

.post-navigation .next-post {
  text-align: right;
}

.previous-post-label, .next-post-label {
  color: #9e9e9e;
  font-size: 14px;
  text-transform: uppercase;
}

.previous-post-title, .next-post-title {
  color: #454545;
  font-size: 18px;
  font-weight: 700;
}

.tag-title {
  font-size: 24px;
  color: #e3b536;
}

.tag-header, .tag-posts {
  margin: 40px 5vw;
}

@media (max-width: 1400px) {
  .site-navigation {
    padding: 20px 15vw;
  }

  .latest-posts, .tag-header, .tag-posts {
    margin: 40px 0;
  }
}

@media (max-width: 1200px) {
  .site-navigation {
    padding: 20px 8vw;
  }

  .site-main {
    margin: 0 12vw;
  }

  .post-full-header {
    margin: 40px 0;
  }

  .post-full-content {
    margin: 40px 0;
  }
}

@media (min-width: 1000px) {
  .nav-menu-icon {
    display: none;
  }
}

@media (max-width: 1000px) {
  .nav-content {
    background-color: white;
    display: none;
    position: absolute;
    right: 8vw;
    top: 48px;
    transform: translateX(280px);
    transition: transform 250ms;
    z-index: 10;
  }

  .nav-content.active {
    display: block;
    transform: translateX(0);
  }

  .nav-content .nav {
    display: block;
    padding: 8px 36px;
  }

  .nav-content .nav li {
    margin: 24px 0;
  }

  .nav-menu-icon {
    display: none;
    font-size: 28px;
    cursor: pointer;
  }

  .nav-menu-icon.active {
    display: block;
  }
}

@media (max-width: 800px) {
  .post-full-header .post-full-title {
    font-size: 36px;
  }

  .post-navigation {
    display: none;
  }
}

@media (max-width: 400px) {
  .featured-post-content {
    bottom: 24px;
  }

  .featured-post-content .post-card-title {
    font-size: 36px;
    width: 85%;
  }

  .featured-post-content .post-tagline {
    width: 85%;
  }

  .featured-post-content .post-card-excerpt {
    width: 85%;
  }

  .post-tagline {
    font-size: 12px;
  }
}